<template>
  <div class="sales-board">
    <div class="sales-board-intro">
      <h2></h2>
      <p>
        </p>
    </div>
    <div class="sales-board-form">
      <div class="sales-board-line">
        <div class="sales-board-line-left">
          
        </div>
        <div class="sales-board-line-right">
          <counter :selections="buyNum" :max="6" :min="1" @on-change="changeParamer('buyNum', $event)"></counter>
        </div>
      </div>
      <div class="sales-board-line">
        <div class="sales-board-line-left">
          
        </div>
        <div class="sales-board-line-right">
          <selection :selections="buyTypes" @on-change="changeParamer('buyType', $event)"></selection>
        </div>
      </div>
      <div class="sales-board-line">
        <div class="sales-board-line-left">
          
        </div>
        <div class="sales-board-line-right">
          <chooser :selections="periodList" @on-change="changeParamer('period', $event)"></chooser>
        </div>
      </div>
      <div class="sales-board-line">
        <div class="sales-board-line-left">
          
        </div>
        <div class="sales-board-line-right">
          <mul-chooser :selections="versionList" @on-change="changeParamer('versions', $event)"></mul-chooser>
        </div>
      </div>
      <div class="sales-board-line">
        <div class="sales-board-line-left">
          
        </div>
        <div class="sales-board-line-right">
          {{ price }} 
        </div>
      </div>
      <div class="sales-board-line">
        <div class="sales-board-line-left">&nbsp;</div>
        <div class="sales-board-line-right">
          <div class="button" @click="showPayDialog">
            
          </div>
        </div>
      </div>
    </div>
    <div class="sales-board-des">
      <h2></h2>
      <p></p>

      <h3></h3>
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>

      <h3></h3>
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
    </div>
    <o-dialog :is-show="isShowPayDialog" @on-close="hidePayDialog">
      <table class="buy-dialog-table">
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr>
          <td>{{ buyNum }}</td>
          <td>{{ buyType.label }}</td>
          <td>{{ period.label }}</td>
          <td>
            <span v-for="item in versions">{{ item.label }}</span>
          </td>
          <td>{{ price }}</td>
        </tr>
      </table>
      <h3 class="buy-dialog-title"></h3>
      <bank-chooser @on-change="changeBanks"></bank-chooser>
      <div class="button buy-dialog-btn" @click="confirmBuy()">
        
      </div>
    </o-dialog>
    <check-order :is-show-check-dialog="isShowCheckOrder" @on-close-check-dialog="hideCheckOrder()"></check-order>
    <o-dialog :is-show="isShowErrDialog" @on-close="hideErrDialog()">
      
    </o-dialog>
  </div>
</template>

<script>
  import Selection from '../../components/base/selection'
  import Counter from '../../components/base/counter'
  import Chooser from '../../components/base/chooser'
  import MulChooser from '../../components/base/multyplychooser'
  import oDialog from '../../components/dialog'
  import BankChooser from '../../components/bankchooser'
  import CheckOrder from '../../components/checkorder'
  import _ from 'lodash'

  export default {
    components: {
      Selection,
      Counter,
      Chooser,
      MulChooser,
      oDialog,
      BankChooser,
      CheckOrder
    },
    data() {
      return {
        buyNum: 0,
        buyType: {},
        buyTypes: [
          {
            label: '',
            value: 0
          },
          {
            label: '',
            value: 1
          },
          {
            label: '',
            value: 2
          }
        ],
        versions: [],
        versionList: [
          {
            label: '',
            value: 0
          },
          {
            label: '',
            value: 1
          },
          {
            label: '',
            value: 2
          }
        ],
        period: {},
        periodList: [
          {
            label: '',
            value: 0
          },
          {
            label: '',
            value: 1
          },
          {
            label: '',
            value: 2
          }
        ],
        price: 0,

        isShowPayDialog: false,
        bankId: null,
        isShowCheckOrder: false,
        orderId: null,
        isShowErrDialog: false
      }
    },
    methods: {
      changeParamer(attr, val) {
        this[attr] = val;
        this.getPrice()
      },
      getPrice() {
        let buyVersionsArray = _.map(this.versions, (item) => {
          return item.value
        });
        let reqParams = {
          buyNumber: this.buyNum,
          buyType: this.buyType,
          period: this.period,
          versions: buyVersionsArray.join(',')
        };
        this.$http.post('/api/total', reqParams).then(
          (res) => {
            this.price = res.data.data.amount
          }
        )
      },
      showPayDialog() {
        this.isShowPayDialog = true
      },
      hidePayDialog() {
        this.isShowPayDialog = false
      },
      changeBanks(bankObj) {
        this.bankId = bankObj.id
      },

      confirmBuy() {
      let reqParams = {
        total:this.price,
        bankId: this.bankId
      };
        this.$http.post('/api/order', reqParams).then(
          (res) => {
            this.orderId = res.data.data.orderId;
            this.isShowCheckOrder = true;
            this.isShowPayDialog = false
          },
          (err) => {
            this.isShowBuyDialog = false;
            this.isShowErrDialog = true
          }
        )
      }
    },
    hideErrDialog() {
      this.isShowErrDialog = false
    },
    hideCheckOrder() {
      this.isShowCheckOrder = false
    },
    mounted() {
      this.buyNum = 1;
      this.buyType = {
        label: '',
        value: 0
      };
      this.versions = [];
      this.period = {
        label: '',
        value: 0
      };
      this.price = 0
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .buy-dialog-title {
    font-size: 16px;
    font-weight: bold;
  }

  .buy-dialog-btn {
    margin-top: 20px;
  }

  .buy-dialog-table {
    width: 100%;
    margin-bottom: 20px;
  }

  .buy-dialog-table td,
  .buy-dialog-table th {
    border: 1px solid #e3e3e3;
    text-align: center;
    padding: 5px 0;
  }

  .buy-dialog-table th {
    background: #4fc08d;
    color: #fff;
    border: 1px solid #4fc08d;
  }
</style>



